* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body {
  font-family: "Noto Sans KR", system-ui, -apple-system, "Segoe UI", Roboto,
    "Helvetica Neue", Arial;
  background: #fafafa;
  color: #111827;
}

.app-wrap {
  padding: 24px 36px;
  position: relative;
  max-width: 1180px;
  margin: 0 auto;
}

.app-header {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  border-bottom: 1px solid #e6e9ee;
  padding-bottom: 12px;
  margin-bottom: 20px;
}
.app-title {
  font-size: 28px;
  font-weight: 800;
  color: #0f1724;
  margin-bottom: 6px;
}
.app-subtitle {
  font-size: 14px;
  color: #6b7280;
  margin-bottom: 8px;
}

.app-header-icons {
  position: absolute;
  top: 22px;
  right: 32px;
  display: flex;
  gap: 16px;
  align-items: center;
}
.header-heart svg,
.header-cart svg {
  width: 28px;
  height: 28px;
  display: block;
}
.header-cart {
  position: relative;
  cursor: pointer;
}
.cart-badge {
  position: absolute;
  top: -6px;
  right: -8px;
  background: #ec4899;
  color: #fff;
  width: 18px;
  height: 18px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 12px;
  font-weight: 700;
}

.no-products-text {
  text-align: center;
  color: #6b7280;
  margin-top: 36px;
}

.grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 24px;
  justify-items: center;
  align-items: start;
  width: 100%;
}

@media (max-width: 1200px) {
  .grid {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (max-width: 900px) {
  .grid {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 520px) {
  .grid {
    grid-template-columns: repeat(1, 1fr);
    padding: 0 8px;
  }
}

.grid-item {
  width: 100%;
  display: flex;
  justify-content: center;
}

html,
body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
