.pcard {
  position: relative;
  background: #fff;
  border-radius: 14px;
  overflow: hidden;
  box-shadow: 0 6px 16px rgba(16, 24, 40, 0.06);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  max-width: 280px;
  height: 440px;
  transition: transform 0.25s ease, box-shadow 0.25s ease;
}

.pcard:hover {
  transform: translateY(-4px);
  box-shadow: 0 8px 18px rgba(16, 24, 40, 0.1);
}

/* 왼쪽 상단 번호 */
.pcard-badge {
  position: absolute;
  top: 12px;
  left: 12px;
  background: #0f172a;
  color: #fff;
  width: 26px;
  height: 26px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  font-size: 13px;
  z-index: 6;
}

/* 오른쪽 상단 하트 */
.pcard-top-right {
  position: absolute;
  top: 10px;
  right: 10px;
  display: flex;
  align-items: center;
  gap: 8px;
  z-index: 6;
}
.pcard-heart-btn {
  background: transparent;
  border: none;
  padding: 4px;
  cursor: pointer;
}
.pcard-heart-btn svg {
  width: 20px;
  height: 20px;
}
.pcard-like-count {
  font-size: 12px;
  color: #6b7280;
}

/* ✅ 이미지 여백 완전 제거 */
.pcard-image-wrap {
  width: 100%;
  height: 250px;
  background: #f5f6f8;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

.pcard-image {
  width: 100%;
  height: 100%;
  object-fit: cover; /* 여백 없이 꽉 차게 */
  display: block;
  transition: transform 0.25s ease;
}

/* hover 시 살짝 확대 */
.pcard:hover .pcard-image {
  transform: scale(1.05);
}

/* ✅ 콘텐츠 영역 */
.pcard-content {
  flex: 1;
  padding: 16px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.pcard-info {
  display: flex;
  flex-direction: column;
  gap: 6px;
}
.pcard-title {
  font-size: 15px;
  font-weight: 700;
  color: #0f172a;
  line-height: 1.3;
}
.pcard-brand {
  font-size: 13px;
  color: #6b7280;
}

.pcard-price-row {
  display: flex;
  gap: 10px;
  align-items: center;
  margin-top: 6px;
}
.pcard-price {
  font-weight: 700;
  color: #111827;
  font-size: 14px;
}
.pcard-palette {
  display: flex;
  gap: 5px;
}
.color-dot {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  border: 1px solid rgba(0, 0, 0, 0.08);
}

/* ✅ 버튼 디자인 */
.pcard-buttons {
  display: flex;
  gap: 10px;
  margin-top: 14px;
}
.pcard-add,
.pcard-buy {
  flex: 1;
  padding: 8px 0;
  font-weight: 700;
  font-size: 13px;
  border-radius: 8px;
  cursor: pointer;
}
.pcard-add {
  background: #fff;
  border: 1px solid #e6e9ee;
  color: #0f1724;
}
.pcard-buy {
  background: #0f1724;
  color: #fff;
  border: none;
}

/* ✅ 반응형 */
@media (max-width: 900px) {
  .pcard {
    height: 410px;
  }
  .pcard-image-wrap {
    height: 240px;
  }
}
@media (max-width: 600px) {
  .pcard {
    max-width: 100%;
    height: 380px;
  }
  .pcard-image-wrap {
    height: 210px;
  }
}
